body {
  background-color: #FAFAFA;
  padding:0;
  margin: 0;
  font-family: sans-serif;
}
.container {
  width: 100%;
  height: 100vh;
  position: relative;
  margin: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}
.ball {
  position: relative;
  width: 60px;
  height: 60px;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: black;
  border-radius: 50%;
  border: 3px solid #212121;
  color: white;
  text-align: center;
  font-size: 1.4em;
  contain: layout;
}
.btn__wrapper {
  /*border: 1px solid black;*/
  position: relative;
  width: auto;
  height: auto;
  margin: 0 auto;
}
@-webkit-keyframes fadeinout {
  0% {
    opacity: 0;
  }
  25% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes fadeinout {
  0% {
    opacity: 0;
  }
  25% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
.action__btn {
  outline: none;
  width: auto;
  height: auto;
  z-index: 2;
  padding: 10px 25px;
  /*max-height: 65px;*/
  background-color: transparent;
  border: 3px solid #1a7076;
  color: #1a7076;
  text-align: center;
  text-transform: uppercase;
  cursor: pointer;
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
  font-weight: 400;
  line-height: 2;
  text-decoration: none;
}
.bg {
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 0;
  z-index: -1;
  cursor: pointer;
  background-color: #1a7076;
  -webkit-transform: translateY(-100%);
      -ms-transform: translateY(-100%);
          transform: translateY(-100%);
}
.action__btn:active + .bg {
  /*opacity: 1;*/
  -webkit-animation-name: fadeinout;
          animation-name: fadeinout;
  -webkit-animation-duration: 200ms;
          animation-duration: 200ms;
}
 /* The switch - the box around the slider */
.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
  overflow: hidden;
}

/* Hide default HTML checkbox */
.switch input {visibility: hidden;}

/* The slider */
.slider__1 {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
  background-color: #ccc;
}
.slider__2 {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 20;
  opacity: 0;
  z-index: 2;
  background-color: #54E46C;
}
/* circle */
.circle {
  position: absolute;
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  cursor: pointer;
  background-color: #FFFFFF;
  z-index: 3;
  -webkit-box-shadow: 0px 2px 4px rgba(0,0,0,0.3);
          box-shadow: 0px 2px 4px rgba(0,0,0,0.3)
}

input:checked + .slider__2 {
  opacity: 1;
}

/* Rounded sliders */
.slider__1.round,
.slider__2.round {
  border-radius: 34px;
}
.circle {
  border-radius: 50%;
}



